.main {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    z-index: 12;
    background: #f1f1f1;
    overflow: hidden;
    transition: top 1s ease-out;
}

.bubble {
    position: absolute;
    left: calc(var(--x) * 1px);
    top: calc(var(--y) * 1px);
    width: calc(var(--size) * 1px);
    height: calc(var(--size) * 1px);
    animation: animate var(--duration) linear infinite;
    animation-delay: var(--delay);
    background: #ff2a2a;
    z-index: 2;
    border-radius: 50%;
}

@keyframes animate {
    0% {
        transform: translateY(0);
        opacity: 0;
    }
    10% {
        opacity: 1;
    }
    90% {
        opacity: 1;
    }
    100% {
        transform: translateY(-2000%);
        opacity: 0;
    }
}

.bubble:nth-child(even) {
    background: #f1f1f1;
    border: 1px solid #ff2a2a;
}

.box {
    width: 450px;
    height: 450px;
    position: relative;
    z-index: 3;
}

.hide {
    top: -100vh;
}

.box:before {
    content: '';
    position: absolute;
    top: 0;
    left: 50%;
    width: 100%;
    height: 100%;
    transform: rotate(45deg);
    background: linear-gradient(to right, #000, transparent);
    opacity: .1;
    transform-origin: left;
}

.circle {
    position: relative;
    background: linear-gradient(135deg, #fff, #e4e3e8);
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    z-index: 1;
}

.circleLoad {
    cursor: pointer;
}

.circle:before {
    content: '';
    position: absolute;
    top: 5px;
    left: 5px;
    bottom: 5px;
    right: 5px;
    background: linear-gradient(-45deg, #fff, #e4e3e8);
    z-index: 1;
    border-radius: 50%;
}

.circle h2 {
    font-family: 'Lobster', cursive;
    font-size: 4em;
    max-width: 80%;
    text-align: center;
    color: #ff2a2a;
    z-index: 10;
}

.circle p {
    position: absolute;
    bottom: 100px;
    left: 50%;
    transform: translate(-50%, -50%);
    font-family: 'Lobster', cursive;
    font-size: 15px;
    max-width: 80%;
    text-align: center;
    opacity: .5;
    color: #ff2a2a;
    z-index: 10;
}