html, body, * {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

body {
    overflow: hidden;
}

.app {
    text-align: center;
    min-height: 100vh;
    position: relative;
    overflow: hidden;
}

.container {
    overflow: hidden;
    height: 100%;
}

.fireplace {
    position: absolute;
    right: 350px;
    bottom: 23.5%;
    z-index: 1;
}

.fireContainer {
    position: relative;
    width: 0;
    height: 0;
}

.armchair {
    position: absolute;
    right: 50px;
    bottom: 13.5%;
    width: 350px;
    height: 333px;
    z-index: 1;
    transition: right 1s ease 1s;
}

.armchairHidden {
    right: -350px;
}

.fire {
    position: absolute;
    width: 200px;
    height: 240px;
    background: radial-gradient(#d8851d, transparent, transparent);
    border-radius: 50%;
    top: 300px;
    left: 255px;
    transform: translate(-50%, -50%);
    animation: fire 2s infinite linear alternate;
}

@keyframes fire {
    0% {
        transform: translate(-50%, -50%) scale(0.8);
    }
    25% {
        transform: translate(-50%, -50%) scale(1);
    }
    50% {
        transform: translate(-50%, -50%) scale(1.4);
    }
    75% {
        transform: translate(-50%, -50%) scale(1);
    }
    100% {
        transform: translate(-50%, -50%) scale(1.2);
    }
}

.floor {
    position: absolute;
    right: 0;
    bottom: 0;
    min-width: 100%;
    object-fit: contain;
    object-position: center;
}

.tree {
    position: absolute;
    left: 10%;
    bottom: 15%;
    width: 450px;
    z-index: 2;
    transition: left 1s ease 2s;
}

.treeHidden {
    left: -450px !important;
}

.partition {
    position: absolute;
    z-index: 11;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: radial-gradient(circle at calc(100% - 585px) 52%, transparent, rgba(51, 51, 51, 0.9),
    rgba(51, 51, 51, 0.99), #333, #333, #333);
}

.partition_start {
    animation: partition 2s ease 1;
    animation-delay: 1s;
    animation-fill-mode: forwards;
}

@keyframes partition {
    0% {
        background-position: 0 0;
        background-size: 100vw 100vh;
    }
    100% {
        background-size: 1000vw 1000vh;
        background-position: -900vw -500vh;
    }
}

.gifts_tree {
    position: absolute;
    left: 25%;
    bottom: 12%;
    z-index: 2;
    transition: bottom 1.1s 2.3s ease-in;
}

.gifts_tree_hidden {
    bottom: -200px !important;
}

.gifts {
    position: absolute;
    bottom: -30%;
    right: 10%;
    z-index: 10;
    object-fit: cover;
    width: 100%;
    transition: bottom 1s ease 2.9s;
}

.gifts_hidden {
    bottom: -1035px !important;
}

.festoon {
    position: absolute;
    width: 70%;
    top: -300px;
    left: 50%;
    transform: translateX(-50%);
    object-fit: cover;
}

.titleContainer {
    position: absolute;
    top: 5%;
    left: 50%;
    transform: translateX(-50%);
}

.title {
    font-family: 'Lobster';
}

.title > span {
    display: inline-block;
    -webkit-text-stroke: 1px #000000;
    text-stroke: 1px #000;
    font-size: 60px;
    font-family: 'Fira Sans', sans-serif;
    opacity: 0;
    transform: translateY(-300px);
}

.titleStart > span {
    animation: title 1.5s cubic-bezier(0.25, 1.5, 0.75, 1.5) 2s;
    animation-delay: calc(3s + var(--i) * .2s);
    animation-fill-mode: forwards;
    color: #bff0cc;
}

@keyframes title {
    0% {
        transform: translateY(-300px);
        opacity: 0;
    }
    100% {
        transform: translateY(0);
        opacity: 1;
    }
}

.wreath {
    position: absolute;
    top: 0;
    transition: transform 1s cubic-bezier(0.25, 1.5, 0.75, 1.5) 2s;
}

.wreath_left {
    left: 0;
    transform: translate(-100%, -100%);
}

.wreath_left_start {
    transform: translate(-30%, -50%);
}

.wreath_right {
    right: 0;
    transform: translate(100%,-100%);
}

.wreath_right_start {
    transform: translate(30%,-50%);
}